import React, { useState, useEffect } from 'react';
import './Dreams.scss';

const Dreams = () => {
  // Load dreams data from localStorage or use default
  const [dreamsData, setDreamsData] = useState(() => {
    const savedDreams = localStorage.getItem('dreamsData');
    return savedDreams ? JSON.parse(savedDreams) : {
      past: [
        { id: 0, date: "Feb 2024", content: "Goal" },
      ],
      present: [
        { id: 1, date: "By Sep 1", content: "Write a book, 200 pages" },
      ],
      future: [
        { id: 2, date: "2026", content: "Get 4k salary + 20k invested" },      
      ],
      timeless: [
        { id: 3, content: "Become immortal" },
      ]
    };
  });

  const [completedPastItems, setCompletedPastItems] = useState(() => {
    const savedCompletedItems = localStorage.getItem('completedDreamItems');
    return savedCompletedItems ? JSON.parse(savedCompletedItems) : [];
  });

  const [editingId, setEditingId] = useState(null);
  const [editForm, setEditForm] = useState({ date: '', content: '' });
  const [showImportExport, setShowImportExport] = useState(false);
  const [importError, setImportError] = useState(null);
  const [jsonData, setJsonData] = useState('');

  // Save data to localStorage whenever it changes
  useEffect(() => {
    localStorage.setItem('dreamsData', JSON.stringify(dreamsData));
    localStorage.setItem('completedDreamItems', JSON.stringify(completedPastItems));
  }, [dreamsData, completedPastItems]);

  const handleExport = () => {
    const dataToExport = {
      dreamsData,
      completedPastItems
    };
    const dataStr = JSON.stringify(dataToExport, null, 2);
    const blob = new Blob([dataStr], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = 'dreams-data.json';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };


  const handleImport = (e) => {
    const file = e.target.files[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = (event) => {
      try {
        const importedData = JSON.parse(event.target.result);
        if (importedData.dreamsData && importedData.completedPastItems) {
          setDreamsData(importedData.dreamsData);
          setCompletedPastItems(importedData.completedPastItems);
          setImportError(null);
          setShowImportExport(false);
        } else {
          setImportError('Invalid data format');
        }
      } catch (error) {
        setImportError('Failed to parse JSON file');
      }
    };
    reader.readAsText(file);
  };

  const togglePastItem = (id) => {
    setCompletedPastItems(prev => 
      prev.includes(id) 
        ? prev.filter(itemId => itemId !== id) 
        : [...prev, id]
    );
  };

  const startEditing = (dream, category) => {
    setEditingId({ id: dream.id, category });
    setEditForm({ date: dream.date || '', content: dream.content });
  };

  const saveEdit = () => {
    if (!editingId) return;

    setDreamsData(prev => {
      const newData = { ...prev };
      const categoryArray = [...newData[editingId.category]];
      const index = categoryArray.findIndex(item => item.id === editingId.id);
      
      if (index !== -1) {
        categoryArray[index] = { 
          ...categoryArray[index], 
          date: editForm.date,
          content: editForm.content
        };
        newData[editingId.category] = categoryArray;
      }
      
      return newData;
    });

    setEditingId(null);
    setEditForm({ date: '', content: '' });
  };

  const cancelEdit = () => {
    setEditingId(null);
    setEditForm({ date: '', content: '' });
  };

  const addNewDream = (category) => {
    const newId = Math.max(...dreamsData[category].map(d => d.id), 0) + 1;
    const newDream = { id: newId, date: '', content: '' };
    
    // Add new dream at the beginning of the array instead of the end
    setDreamsData(prev => ({
      ...prev,
      [category]: [newDream, ...prev[category]]
    }));
    
    setEditingId({ id: newId, category });
    setEditForm({ date: '', content: '' });
    
    // Scroll to top of the dream list
    const dreamList = document.querySelector(`.${category.replace(' ', '-')} .dream-list`);
    if (dreamList) {
      dreamList.scrollTop = 0;
    }
  };

  const deleteDream = (id, category) => {
    setDreamsData(prev => ({
      ...prev,
      [category]: prev[category].filter(dream => dream.id !== id)
    }));
    
    setCompletedPastItems(prev => prev.filter(itemId => itemId !== id));
  };

  const exportData = () => {
    const dataToExport = {
      dreamsData,
      completedPastItems
    };
    const dataStr = JSON.stringify(dataToExport, null, 2);
    setJsonData(dataStr);
  };

  const importData = () => {
    try {
      const importedData = JSON.parse(jsonData);
      if (importedData.dreamsData && importedData.completedPastItems) {
        setDreamsData(importedData.dreamsData);
        setCompletedPastItems(importedData.completedPastItems);
        setJsonData('');
      }
    } catch (e) {
      alert('Invalid JSON format');
    }
  };

  const renderDreamCard = (dream, category) => {
    if (editingId?.id === dream.id && editingId?.category === category) {
      return (
        <div className="dream-card editing">
          <input
            type="text"
            value={editForm.date}
            onChange={(e) => setEditForm({...editForm, date: e.target.value})}
            placeholder="Date"
          />
          <textarea
            value={editForm.content}
            onChange={(e) => setEditForm({...editForm, content: e.target.value})}
            placeholder="Content"
          />
          <div className="edit-controls">
            <button onClick={saveEdit}>Save</button>
            <button onClick={cancelEdit}>Cancel</button>
          </div>
        </div>
      );
    }

    const isPast = category === 'past';
    const isCompleted = isPast && completedPastItems.includes(dream.id);

    return (
      <div 
        key={`${category}-${dream.id}`} 
        className={`dream-card ${isCompleted ? 'completed' : ''}`}
        onClick={() => isPast && togglePastItem(dream.id)}
      >
        <div className="dream-header">
          {dream.date && <div className="dream-date">{dream.date}</div>}
          <div className="dream-actions">
            {isPast && (
              <span className="status-toggle">
                {isCompleted ? '' : ''}
              </span>
            )}
            <button onClick={(e) => {
              e.stopPropagation();
              startEditing(dream, category);
            }}>
              Edit
            </button>
            <button onClick={(e) => {
              e.stopPropagation();
              deleteDream(dream.id, category);
            }}>
              Delete
            </button>
          </div>
        </div>
        <div className="dream-content">
          {dream.content.split('\n').map((line, i) => (
            <p key={i}>{line}</p>
          ))}
        </div>
      </div>
    );
  };

  return (
    <div className="dreams-page">
       <div className="data-management">
        <button 
          className="import-export-toggle"
          onClick={() => setShowImportExport(!showImportExport)}
        >
          {showImportExport ? 'Hide Import/Export' : 'Show Import/Export'}
        </button>
        <button className="btn past" onClick={() => addNewDream('past')}>+</button>
        <button className="btn present" onClick={() => addNewDream('present')}>+</button>
        <button className="btn future" onClick={() => addNewDream('future')}>+</button>
        <button className="btn timeless" onClick={() => addNewDream('timeless')}>+</button>

      </div>

      {showImportExport && (
        <div className="import-export-panel">
          <div className="export-section">
            <h3>Export Data</h3>
            <p>Download your dreams data as a JSON file</p>
            <button onClick={handleExport} className="export-button">
              Export to JSON
            </button>
          </div>
          
          <div className="import-section">
            <h3>Import Data</h3>
            <p>Upload a JSON file to import dreams</p>
            <label className="import-button">
              Choose JSON File
              <input 
                type="file" 
                accept=".json" 
                onChange={handleImport} 
                style={{ display: 'none' }} 
              />
            </label>
            {importError && <div className="import-error">{importError}</div>}
          </div>
        </div>
      )}

      <div className="dreams-grid">
        <div className="dream-column past-achievements">
          <h2>Past</h2>
          <div className="dream-list">
            {dreamsData.past.map(dream => renderDreamCard(dream, 'past'))}
          </div>
        </div>

        <div className="dream-column present-goals">
          <h2>Present</h2>
          <div className="dream-list">
            {dreamsData.present.map(dream => renderDreamCard(dream, 'present'))}
          </div>
        </div>

        <div className="dream-column future-plans">
          <h2>Future</h2>
          <div className="dream-list">
            {dreamsData.future.map(dream => renderDreamCard(dream, 'future'))}
          </div>
        </div>

        <div className="dream-column timeless-dreams">
          <h2>Timeless Dreams</h2>
          <div className="dream-list">
            {dreamsData.timeless.map(dream => renderDreamCard(dream, 'timeless'))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Dreams;